'use strict';

                                                                                       
import Platform from '../Utilities/Platform';
import RCTDeviceEventEmitter from './RCTDeviceEventEmitter';
import invariant from 'invariant';
                        
                                       
                                       
 
                                  
export default class NativeEventEmitter                                                                   {
  _nativeModule               ;
  constructor(nativeModule               ) {
    if (Platform.OS === 'ios') {
      invariant(nativeModule != null, '`new NativeEventEmitter()` requires a non-null argument.');
    }
    const hasAddListener = !!nativeModule && typeof nativeModule.addListener === 'function';
    const hasRemoveListeners = !!nativeModule && typeof nativeModule.removeListeners === 'function';
    if (nativeModule && hasAddListener && hasRemoveListeners) {
      this._nativeModule = nativeModule;
    } else if (nativeModule != null) {
      if (!hasAddListener) {
        console.warn('`new NativeEventEmitter()` was called with a non-null argument without the required `addListener` method.');
      }
      if (!hasRemoveListeners) {
        console.warn('`new NativeEventEmitter()` was called with a non-null argument without the required `removeListeners` method.');
      }
    }
  }
  addListener                                (eventType        , listener                                                           , context        )                    {
    this._nativeModule?.addListener(eventType);
    let subscription                     = RCTDeviceEventEmitter.addListener(eventType, listener, context);
    return {
      remove: () => {
        if (subscription != null) {
          this._nativeModule?.removeListeners(1);
          subscription.remove();
          subscription = null;
        }
      }
    };
  }
  emit                                (eventType        , ...args                                       )       {
    RCTDeviceEventEmitter.emit(eventType, ...args);
  }
  removeAllListeners                                (eventType          )       {
    invariant(eventType != null, '`NativeEventEmitter.removeAllListener()` requires a non-null argument.');
    this._nativeModule?.removeListeners(this.listenerCount(eventType));
    RCTDeviceEventEmitter.removeAllListeners(eventType);
  }
  listenerCount                                (eventType        )         {
    return RCTDeviceEventEmitter.listenerCount(eventType);
  }
}